  body {
    position:relative;
    background-color: #262626;
    margin: 0;
    padding: 0;
    min-height: 100vh;
    
  }
canvas {
     height:100%;
     width: 100%;
     background: #262626;
     position: absolute;
     z-index: -1;
     top: 0;
     left: 0;
     bottom:0;
  }
 
  marquee{
    color:white;
  }
  
  .heading-underline {
    border-bottom: 1px solid #262626;
    padding-bottom: .5em;
  }
  
  .heading-boxed {
    background:  #262626;
    border: 1px solid #262626;
    padding: .5em 1rem;
  }
  
  
  
  // ----
  // Sub headings
  // ----
  .sub-heading {
    color:   Green;
    font-size: .8em;
    font-weight: 500;
  }
  
  .sub-heading--stacked {
    display: block;
    margin-top: .1em;
  }
  
  
  
  // ----
  // Text alignments
  // ----
  .heading-alignment--left { text-align: left; }
  .heading-alignment--right { text-align: right; }
  .heading-alignment--center { text-align: center; }
  
  
  
 